import React, { useState } from "react";
import "./about.css";

import { Container, Row, Col } from "reactstrap";

import aboutImg from "../../assests/images/hero-img.png";

import Education from "./Education";

import Skills from "./Skills";

import Award from "./Award";

const About = () => {
  const [aboutFilter, setAboutFilter] = useState("ABOUT");

  return (
    <section id="about">
      <Container>
        <Row>
          <Col lg="12" className="mb-5">
            <h2>About Me</h2>
          </Col>

          <Col lg="4" md="3">
            <div className="about__btns d-flex flex-column align-items-center">
              <button
                className={`about__btn ${
                  aboutFilter === "ABOUT" ? "about__btn-active" : ""
                }`}
                onClick={() => setAboutFilter("ABOUT")}
              >
                About Me
              </button>
              <button
                className={`about__btn ${
                  aboutFilter === "EDUCATION" ? "about__btn-active" : ""
                }`}
                onClick={() => setAboutFilter("EDUCATION")}
              >
                Education
              </button>
              <button
                className={`about__btn ${
                  aboutFilter === "SKILLS" ? "about__btn-active" : ""
                }`}
                onClick={() => setAboutFilter("SKILLS")}
              >
                Skills
              </button>
              <button
                className={`about__btn ${
                  aboutFilter === "AWARD" ? "about__btn-active" : ""
                }`}
                onClick={() => setAboutFilter("AWARD")}
              >
                Award
              </button>
            </div>
          </Col>

          <Col lg="8" md="9">
            {aboutFilter == "ABOUT" && (
              <div className="about__content__wrapper d-flex gap-5">
                <div className="about__img w-25">
                  <img src={aboutImg} alt="" className="w-100" />
                </div>

                <div className="about__content w-75">
                  <h2>I'm Malik Bilal</h2>
                  <p>
                  Hello! I'm Bilal Akbar, Experienced Software Engineer with history of working in Web Industry. With 2 years of experience in the field, I have honed my skills in creating Visually Stunning and Dynamic Websites. 
                  </p>

                  <div className="social__links">
                    <h6 className="mb-3">Connect with me:</h6>
                    <span>
                      <a
                        href="https://www.facebook.com/malikbilalakbar125"
                        target="_blank"
                      >
                        <i className="ri-facebook-line" title="Facebook"></i>
                      </a>
                    </span>
                    <span>
                      <a
                        href="https://www.instagram.com/malikbilalakbar125"
                        target="_blank"
                      >
                        <i className="ri-instagram-line" title="Instagram"></i>
                      </a>
                    </span>
                    <span>
                      <a
                        href="https://twitter.com/malikbilal_125?"
                        target="_blank"
                      >
                        <i className="ri-twitter-line" title="Twitter"></i>
                      </a>
                    </span>
                    <span>
                      <a
                        href="https://github.com/malikbilalakbar125"
                        target="_blank"
                      >
                        <i className="ri-github-fill" title="GitHub"></i>
                      </a>
                    </span>
                    <span>
                      <a
                        href="https://www.linkedin.com/in/malikbilalakbar125"
                        target="_blank"
                      >
                        <i className="ri-linkedin-fill" title="Linkedin"></i>
                      </a>
                    </span>
                    <span>
                      <a
                        href="https://www.youtube.com/@malikbilalakbar125"
                        target="_blank"
                      >
                        <i className="ri-youtube-line" title="YouTube"></i>
                      </a>
                    </span>
                  </div>
                </div>
              </div>
            )}

            {aboutFilter == "EDUCATION" && <Education />}

            {aboutFilter == "SKILLS" && <Skills />}

            {aboutFilter == "AWARD" && <Award />}
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default About;
