import React from "react";
import "./skills.css";

const frontendSkills = [
  {
    title: "HTML & HTML5",

    percentage: "90%",
  },
  {
    title: "CSS & CSS3",

    percentage: "90%",
  },
  {
    title: "Tailwind CSS & Bootstrap",

    percentage: "85%",
  },
  {
    title: "JavaScript, React & Angular ",

    percentage: "85%",
  },
];

const backendSkills = [
  {
    title: "Node.js",
    width: "85%",
    percentage: "85%",
  },
  {
    title: "Express.js",

    percentage: "85%",
  },
  {
    title: "MongoDB",

    percentage: "85%",
  },
  {
    title: "C-Sharp",

    percentage: "85%",
  },
];

const Skills = () => {
  return (
    <div className="skills__wrapper d-flex gap-5">
      <div className="frontend__skill w-50">
        {frontendSkills.map((item, index) => (
          <SkillItem
            key={index}
            title={item.title}
            percentage={item.percentage}
          />
        ))}
      </div>

      <div className="backend__skill w-50">
        {backendSkills.map((item, index) => (
          <SkillItem
            key={index}
            title={item.title}
            percentage={item.percentage}
          />
        ))}
      </div>
    </div>
  );
};

const SkillItem = ({ title, percentage }) => {
  return  <div className="skill__data mb-3">
    <div className="skill__title d-flex align-items-center justify-content-between">
      <h6>{title}</h6>
      <span>{percentage}</span>
    </div>

    <div className="skill__bar">
      <span
        className="skill__bar-percentage"
        style={{ width: `${percentage}` }}
      ></span>
    </div>
  </div>;
};

export default Skills;
