import React, { useRef, useEffect } from "react";
import "./hero-section.css";

import { Container, Row, Col } from "reactstrap";

import Typed from "typed.js";

import heroimg from "../../assests/images/hero-img.png";

const HeroSection = () => {

  const textRef = useRef();

  useEffect(() => {
    const options = {
      backDelay: 1500,
      showCursor: true,
      loop : true,
      strings: [
        "Bilal Akbar",
        "Hafiz-e-Quran",
        "a Web Developer",
        "Content Creator",
        "Social Influencer",
      ],
    };

    const typed = new Typed(textRef.current, options);
  }, []);
  return (
    <section className="hero__section" id="home">
      <Container>
        <Row>
          <Col lg="6" md="6">
            <div className="hero__content">
              <p className="mb-3">Welcome to my world!</p>

              <h2 className="hero__title mb-4">
                I'm <span ref={textRef}></span>
              </h2>
              <p>
              Experience Software Engineer with a history of working in the Software Industry. Highly organized, Self Motivated, Tech Savvy, and a Fast Learner with innate communication skills. As a Software Engineer and Website Developer, I possess a comprehensive skill set that enables me to handle various aspects of the development process.
              </p>

              <div className=" mt-5 hero__btns d-flex align-items-center gap-4 ">
                <button className="btn hire__btn">
                  <a href="https://www.linkedin.com/in/malikbilalakbar125" target="_blank">Hire Me</a>
                </button>
                <button className="btn">Contact</button>
              </div>
            </div>
          </Col>

          <Col lg="6" md="6">
            <div className="hero__img">
              <img src={heroimg} alt="" className="w-100" />
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default HeroSection;
